import createStore from "redux-zero";

const initialState = {
    showModal1: false,
    showModal2: false,
    showModal3: false,
    showModalC1: false,
    showModalC2: false,
    showModalC3: false,
    showModalC4: false,
    showModalC5: false,
}

const store = createStore(initialState);
export default store;


