import React from "react";
import { Grid, Row, Col, Image } from "react-bootstrap";
import FormContact from "./Form";
import MenuContact from "../Menu/Menu_contact";
import change from "../../img/icons/educacion250x125.png";

const Social = () => {
    return (
        <div className="wrapper pt-25">
            <a href="https://www.facebook.com/changeperu/" target="_blank" rel='noopener noreferrer' className='fb'><i className="fa fa-3x fa-facebook-square"></i></a>
            <a href="https://www.youtube.com/channel/UC6dwKWSo_7D9KWUhF8kTSLg" target="_blank" rel='noopener noreferrer' className='ytb'><i className="fa fa-3x fa-youtube-square"></i></a>
            {/*<i className="fa fa-3x fa fa-youtube-square"></i>
            <i className="fa fa-3x fa-snapchat-square"></i>*/}
        </div>
    )
}

const Extra = () => {
    return (
        <Col lg={5} md={5}>
            <Initial />
            {/*<h1> Contáctanos </h1>*/}
            <h4 className='pt-25' >
                Cuéntanos tus proyectos y objetivos para poder brindarte la mejor opción para tu aprendizaje.
            </h4>
            <h4 className='pt-25 hidden-xs'> Trabajando juntos podemos romper esquemas, podemos cambiar el sistema. </h4>
        </Col>
    )
}

const Initial = () => {
    return (
        <section>
            {/*<h1> Change </h1>
            <p> cambiando la educación </p>*/}
            <Image src={change} responsive />
        </section>
    )
}

const Contact = () => {
    return (
        <Grid fluid id='contactView--content'>
            <Row>
                <MenuContact />
                <Col md={11} lg={11} sm={10} xs={12} id='contact'>
                    <Row>
                        <Extra />
                        <FormContact />
                    </Row>
                    <Col lg={6} md={6}>
                        <p className='pt-25' > No olvides visitarnos en nuestras redes </p>
                        <Social />
                    </Col>
                </Col>
            </Row>
        </Grid>
    )
}

export default Contact;


