import React from "react";
import { Col, Carousel } from "react-bootstrap";
import change900 from "../../img/carousel/change630.jpg";
import group900 from "../../img/carousel/group630.jpg";
import programming900 from "../../img/carousel/programming630.jpg";

const Pics = () => {
    return (
        <Col lgOffset={2} lg={8} mdOffset={2} md={8} >
            <Carousel>
                <Carousel.Item>
                    <img alt="900x500" src={change900} />
                    {/*<Carousel.Caption>
                        <h3>First slide label</h3>
                        <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                    </Carousel.Caption>*/}
                </Carousel.Item>
                <Carousel.Item>
                    <img width={900} height={200} alt="900x500" src={group900} />
                </Carousel.Item>
                <Carousel.Item>
                    <img width={900} height={200} alt="900x500" src={programming900} />
                </Carousel.Item>
            </Carousel>
        </Col>
    )
}

export default Pics;


