const ModalChange = ( {open, showModal, close, nombre, info} ) => {
    return(
        <div>
            <Button onClick={open} className='member__link' bsStyle='yellow--BG' > Ver más </Button>

            <Modal show={showModal} onHide={close}>
                <Modal.Header closeButton className='btn-yellow--BG' >
                    <Modal.Title><strong className='upperCase bold'> {nombre} </strong></Modal.Title>
                </Modal.Header>
                <Modal.Body className='justify' >
                    {info}
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={close} bsStyle='yellow--BG' className='white-text bold' >Close</Button>
                </Modal.Footer>
            </Modal>
        </div>
    )
}

const Changer = ({ name, title, open, showModal, close, pic, info }) => {
    return(
        <Col lg={4} md={4} sm={6} xs={6} className="team-grid__member hover">
            <div className="member__info">
                <div className="center-vert-content">
                    <h3 className="member__name">{name}</h3>
                    <p className="member__title">{title}</p>
                    {/*<a className="member__link" href="javascript:void(0)">Read More</a>*/}
                    <ModalChange
                        open={open}
                        showModal={showModal}
                        close={close}
                        nombre={name}
                        info={info}
                    />
                </div>
            </div>
            <img src={pic} alt="Robert Finkel" />
        </Col>
    )
}

const Team = ({showModalC1, showModalC2, showModalC3, showModalC4, showModalC5}) => {
    return (
        <div className="wrapper--team">
            <div className="l-container">
                <div className="team-grid">
                    <Changer 
                        name={'Jose Carlos Manrique'}
                        title={'CEO'}
                        open={() => openchange1()}
                        showModal={showModalC1}
                        close={() => closechange1()}
                        pic={Jota}
                        nombre={'Jose Carlos Manrique'}
                        info={'José Carlos es el líder de este emprendimiento que busca mejorar la educación, tiene más de 10 años de experiencia enseñando inglés. Durante ese tiempo ha formado parte de programas tradicionales de enseñanza y también de programas para inglés con propósitos específicos.\n Además, José ha trabajado de la mano de la Embajada de los Estados Unidos de América, a través de la Oficina Regional de Enseñanza del Inglés, en la capacitación de docentes de inglés a nivel nacional. Debido a su trabajo fue elegido como uno de los 15 peruanos seleccionados para la Iniciativa Jóvenes Líderes de las Américas (YLAI 2017).'}
                    />
                    <Changer
                        name={'Diego Sanz'}
                        title={'Tipógrafo'}
                        pic={Diego}
                        open={() => openchange2()}
                        showModal={showModalC2}
                        close={() => closechange2()}
                        nombre={'Diego Sanz'}
                        info={'Diego es uno de los tipógrafos más reconocidos del país. Se ha encargado de diseñar la marca para los Juegos Panamericanos Lima 2019. Además, comparte su experiencia en talleres que dicta a nivel nacional.'}
                    />
                    <Changer
                        name={'Lourdes Fajardo'}
                        title={'Docente'}
                        pic={Lourdes}
                        open={() => openchange3()}
                        showModal={showModalC3}
                        close={() => closechange3()}
                        nombre={'Lourdes Fajardo'}
                        info={'Lourdes cuenta con más de 10 años de experiencia como docente. Ha formado parte de diversos programas de inglés y tiene experiencia en el diseño de material para cursos de inglés para propósitos específicos.'}
                    />
                    <Changer
                        name={'Rocio Baldarrago'}
                        title={'Docente'}
                        pic={Rocio}
                        open={() => openchange4()}
                        showModal={showModalC4}
                        close={() => closechange4()}
                        nombre={'Rocio Baldarrago'}
                        info={'Rocío cuenta con 10 años de experiencia en la enseñanza del idioma inglés. Además, ha sido docente de español en los Estados Unidos de América. Actualmente, es coordinadora académica de programas preuniversitarios, en los cuales se dicta cursos de inglés enfocado a profesionales.'}
                    />
                    <Changer
                        name={'Jeff Kuhn'}
                        title={'Capacitador'}
                        pic={Jeff}
                        open={() => openchange5()}
                        showModal={showModalC5}
                        close={() => closechange5()}
                        nombre={'Jeff Kuhn'}
                        info={'Jeff es profesor de inglés y también se dedica a capacitar docentes a nivel mundial. Tiene diversas ponencias en eventos internacionales y es un especialista en tecnologías educativas. Tiene un doctorado en diseño instruccional.'}
                    />
                </div>
            </div>
        </div>
    )
}

//export default Team;
const mapToProps = ({ showModalC1, showModalC2, showModalC3, showModalC4, showModalC5 }) => ({ showModalC1, showModalC2, showModalC3, showModalC4, showModalC5 });
export default connect (mapToProps)(Team)


